import React from "react";
import { useHistory } from "react-router-dom";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";

export function MibileMenu() {
    const history = useHistory();

    return (
        <BottomNavigation
            onChange={(_, value) => history.push(value)}
            showLabels
        >
            <BottomNavigationAction label="Главная" value={"/"} />
            <BottomNavigationAction label="Галерея" value={"gallery"} />
            <BottomNavigationAction label="Контакты" value={"contacts"} />
        </BottomNavigation>
    );
}
